import React, { useState, useEffect } from 'react'
import racketService from './service/rackets.js'
import SearchRackets from './components/SearchRackets'
import RacketComparison from './components/RacketComparison'

const App = () => {
  const [rackets, setRackets] = useState([])
  const [displayComparisonTable, setDisplayComparisonTable] = useState(false)
  const [selectedRackets, setSelectedRackets] = useState([])

  useEffect(() => {
    racketService
      .getAll()
      .then(res => {
      setRackets(res)
    })
  }, [])

  const handleSubmit = (e, v) => {
    if (v.length === 2) {
      setSelectedRackets(v)
      setDisplayComparisonTable(true)
    } else {
      setSelectedRackets([])
      setDisplayComparisonTable(false)
    }
  }

  return (
    <>
      <SearchRackets
        rackets={rackets}
        selectedRackets={selectedRackets}
        handleSubmit={handleSubmit}
       />
      
      {displayComparisonTable
        ? <RacketComparison rackets={selectedRackets} />
        : null}
    </>
  )
}

export default App;