import React from 'react'
import TextField from '@material-ui/core/TextField';
import Autocomplete from '@material-ui/lab/Autocomplete';

const SearchRackets = ({ rackets, selectedRackets, handleSubmit }) => {
  return (
    <>
      <Autocomplete
        multiple
        id="rackets-input"
        onChange={handleSubmit}
        options={rackets}
        getOptionLabel={(option) => option.name}
        blurOnSelect
        renderInput={(params) => (
          <TextField
            {...params}
            variant="standard"
            label="Choose two rackets"
            placeholder={
              selectedRackets.length === 2 ? ''
              : selectedRackets.length === 1 ? '+1'
              : 'Choose two rackets'
                
              }
          />
        )}
      />
    </>
  )
}

export default SearchRackets